<script setup>
import Footnote from "@/api/Footnote";
import { reactive, ref, onBeforeMount } from "vue";

const form = reactive({
    deskripsi: '',
    lokasi: {
        judul: '',
        link: ''
    },
    whatsapp: {
        judul: '',
        link: ''
    },
    website: {
        judul: '',
        link: ''
    },
    channel: {
        judul: '',
    },
    instagram: {
        judul: '',
        link: ''
    },
    instagram2: {
        judul: '',
        link: ''
    },
    youtube: {
        judul: '',
        link: ''
    },
})

let id = ref()
const rules = [(value) => !!value || "Kolom harus diisi"]
const inputs = ref()
const message = ref()

async function update() {

    const validate = await inputs.value.validate()

    if (validate.valid) {

        const res = await Footnote.update(id.value, form)
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        })

        message.value = res.message
    }

}

onBeforeMount(async () => {
    const res = await Footnote.all()

    id.value = res[0]._id
    form.deskripsi = res[0].deskripsi
    form.lokasi = res[0].lokasi
    form.website = res[0].website
    form.channel = res[0].channel
    form.whatsapp = res[0].whatsapp
    form.instagram = res[0].instagram
    form.instagram2 = res[0].instagram2
    form.youtube = res[0].youtube
})

</script>

<template>
    <h2>Edit Footnote</h2>

    <v-divider class="mb-4 mt-2"></v-divider>

    <v-alert type="success" class="my-3" closable close-label="Close Alert" v-if="message">
        {{ message }}
    </v-alert>

    <v-form ref="inputs" class="px-6 py-4 border" @submit.prevent="update">
        <v-textarea v-model="form.deskripsi" :rules="rules" label="Deskripsi" variant="underlined"
            prepend-icon="mdi-note"></v-textarea>

        <v-text-field v-model="form.lokasi.judul" :rules="rules" label="Lokasi" variant="underlined"
            prepend-icon="mdi-map-marker-outline"></v-text-field>
        <v-text-field v-model="form.lokasi.link" :rules="rules" label="Lokasi link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-text-field v-model="form.website.judul" :rules="rules" label="Website" variant="underlined"
            prepend-icon="mdi-web"></v-text-field>
        <v-text-field v-model="form.website.link" :rules="rules" label="Website link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-text-field v-model="form.channel.judul" :rules="rules" label="Tv Channel" variant="underlined"
            prepend-icon="mdi-television-classic"></v-text-field>

        <v-text-field v-model="form.whatsapp.judul" :rules="rules" label="Whatsapp" variant="underlined"
            prepend-icon="mdi-whatsapp"></v-text-field>
        <v-text-field v-model="form.whatsapp.link" :rules="rules" label="Whatsapp link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-text-field v-model="form.instagram.judul" :rules="rules" label="Instagram" variant="underlined"
            prepend-icon="mdi-instagram"></v-text-field>
        <v-text-field v-model="form.instagram.link" :rules="rules" label="Instagram link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-text-field v-model="form.instagram2.judul" :rules="rules" label="Instagram" variant="underlined"
            prepend-icon="mdi-instagram"></v-text-field>
        <v-text-field v-model="form.instagram2.link" :rules="rules" label="Instagram link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-text-field v-model="form.youtube.judul" :rules="rules" label="Youtube" variant="underlined"
            prepend-icon="mdi-youtube"></v-text-field>
        <v-text-field v-model="form.youtube.link" :rules="rules" label="Youtube link" variant="underlined"
            prepend-icon="mdi-link"></v-text-field>

        <v-btn type="submit" color="green" class="my-4">Edit</v-btn>
    </v-form>
</template>